.header-color {
    background: #ee9ca7;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #ee9ca7, #ee9ca7);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ee9ca7, #ee9ca7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    font-family: 'Montserrat';
}

.page-content {
    background: rgb(45,45,45);
    margin: 0;
}

.home-page {
    background: rgb(45,45,45);
    position: absolute;
    width: 100%;
    height: auto;
    margin-bottom: 0;
    background-size: cover;
}

.landing-grid {
    width: 100%;
    height: 100%;
    margin: auto;
}

.doodle-img {
    float: right;
    width: 33%;
    padding-top: 4%;
    padding-right: 6%;
    padding-left: 2%;
    padding-bottom: 2%;
}


.banner-text {
    font-family: 'Rubik';
    text-align: left;
    width: 50%;
    height: auto;
    padding-left: 5%;
}

.banner-text h3 {
    font-family: 'Montserrat';
    font-size: 2em;
    font-weight: normal;
    color: white;
    padding-top: 15%;
    width: 70%;
    text-align: left;
    border-bottom: 1px solid #fff;
    line-height: 0.1em;
    margin-bottom:7%;
}

.banner-text h3 span {
    background: #2d2d2d;
    padding: 0 9px;
}

.banner-text h1 {
    font-family: 'Montserrat';
    font-size: 4em;
    font-weight: bold;
    color: white;
    margin-left: 1%;
}

.banner-text h2 {
    font-family: 'Montserrat';
    font-size: 1.75em;
    font-weight: lighter;
    color: white;
    margin-left: 1.5%;
    margin-bottom: 0.3%;
}

.banner-text h4 {
    font-family: 'Montserrat';
    font-size: 20px;
    font-weight: lighter;
    font-style: italic;
    color: white;
    margin-left: 1.5%;
    margin-top: 0.3%;
}

.social-links {
    display: flex;
    justify-content: space-between;
    width: 12%;
    height: auto;
    margin-left: 0.5%;
}

.social-links i {
    color:white;
    font-size: 4em;
    padding: 0.1em;
}

